import React, { useState } from 'react';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';
import { Card, CardHeader } from '../components/Card';
import { Input } from '../components/Input';
import { Button } from '../components/Button';
import { Spinner } from '../components/Spinner';
import api from '../services/api';

const ClienteContainer = styled.div`
  min-height: 100vh;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
`;

const WelcomeCard = styled(Card)`
  max-width: 500px;
  width: 100%;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 25px 45px rgba(0, 0, 0, 0.1);
`;

const LogoPadaria = styled.div`
  text-align: center;
  margin-bottom: 30px;
  
  h1 {
    color: #8B4513;
    font-size: 2.5rem;
    font-weight: bold;
    margin: 0;
    text-shadow: 2px 2px 4px rgba(0,0,0,0.1);
  }
  
  p {
    color: #666;
    font-size: 1.1rem;
    margin: 10px 0 0 0;
  }
`;

const Form = styled.form`
  display: grid;
  gap: 16px;
`;

const ConsentimentoSection = styled.div`
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 8px;
  padding: 20px;
  margin: 20px 0;
`;

const ConsentimentoTitle = styled.h3`
  color: #495057;
  font-size: 1.2rem;
  margin: 0 0 15px 0;
  display: flex;
  align-items: center;
  gap: 8px;
  
  &:before {
    content: "🛡️";
    font-size: 1.3rem;
  }
`;

const ConsentimentoItem = styled.div`
  display: flex;
  align-items: flex-start;
  gap: 12px;
  margin-bottom: 15px;
  padding: 12px;
  background: white;
  border-radius: 6px;
  border: 1px solid #e9ecef;
  
  input[type="checkbox"] {
    margin-top: 2px;
    min-width: 18px;
    min-height: 18px;
  }
`;

const ConsentimentoText = styled.div`
  flex: 1;
  
  strong {
    color: #495057;
    display: block;
    margin-bottom: 4px;
  }
  
  small {
    color: #6c757d;
    line-height: 1.4;
  }
  
  &.obrigatorio {
    opacity: 0.7;
    
    strong:after {
      content: " (Obrigatório)";
      color: #dc3545;
      font-size: 0.8em;
    }
  }
`;

const TermosSection = styled.div`
  background: #e8f4f8;
  border: 1px solid #bee5eb;
  border-radius: 8px;
  padding: 15px;
  margin: 20px 0;
  font-size: 0.9rem;
  line-height: 1.5;
  color: #0c5460;
`;

const ButtonGroup = styled.div`
  display: flex;
  gap: 16px;
  justify-content: space-between;
  margin-top: 30px;
  
  button[type="submit"] {
    background: #28a745 !important;
    
    &:hover {
      background: #218838 !important;
    }
    
    &:disabled {
      background: #6c757d !important;
    }
  }
  
  button[type="button"] {
    background: #6c757d;
    
    &:hover {
      background: #5a6268;
    }
  }
`;

const SuccessMessage = styled.div`
  background-color: #d4edda;
  color: #155724;
  padding: 16px;
  border-radius: 8px;
  margin-bottom: 20px;
  text-align: center;
  border: 1px solid #c3e6cb;
`;

const QRCodeInfo = styled.div`
  text-align: center;
  margin-bottom: 25px;
  padding: 15px;
  background: linear-gradient(45deg, #f8f9fa, #e9ecef);
  border-radius: 10px;
  border: 2px dashed #6c757d;
  
  p {
    margin: 0;
    color: #495057;
    font-size: 1rem;
  }
  
  .qr-icon {
    font-size: 2rem;
    margin-bottom: 10px;
  }
`;

export const CadastroClientePublico = () => {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [showSuccess, setShowSuccess] = useState(false);
  const [formData, setFormData] = useState({
    nome: '',
    email: '',
    telefone: '',
    cpf: '',
    dataNascimento: '',
    endereco: '',
    bairro: '',
    cidade: '',
    cep: ''
  });

  const [consentimentos, setConsentimentos] = useState({
    servicoEssencial: true, // Obrigatório
    marketingEmail: false,
    marketingSMS: false,
    analisesPersonalizadas: false,
    compartilhamentoTerceiros: false,
    cookiesAnalytics: false
  });

  const [aceitouTermos, setAceitouTermos] = useState(false);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    
    // Formatação especial para data de nascimento
    if (name === 'dataNascimento' && value) {
      // Se o valor está em formato ISO (yyyy-mm-dd), converter para dd/mm/yyyy
      if (value.includes('-') && value.length === 10) {
        const [year, month, day] = value.split('-');
        const formattedDate = `${day}/${month}/${year}`;
        setFormData(prev => ({
          ...prev,
          [name]: formattedDate
        }));
        return;
      }
    }
    
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleConsentimentoChange = (key) => {
    if (key === 'servicoEssencial') return; // Não pode ser alterado
    
    setConsentimentos(prev => ({
      ...prev,
      [key]: !prev[key]
    }));
  };

  const validarCPF = (cpf) => {
    const numbers = cpf.replace(/\D/g, '');
    if (numbers.length !== 11) return false;
    
    // Validação básica de CPF
    if (/^(\d)\1{10}$/.test(numbers)) return false;
    
    return true;
  };

  const validarFormulario = () => {
    const { nome, email, telefone, cpf } = formData;
    
    if (!nome.trim()) {
      alert('Nome é obrigatório');
      return false;
    }
    
    if (!email.trim() || !email.includes('@')) {
      alert('Email válido é obrigatório');
      return false;
    }
    
    if (!telefone.trim()) {
      alert('Telefone é obrigatório');
      return false;
    }
    
    if (!cpf.trim() || !validarCPF(cpf)) {
      alert('CPF válido é obrigatório');
      return false;
    }
    
    if (!aceitouTermos) {
      alert('Você deve aceitar os termos de uso e política de privacidade');
      return false;
    }
    
    return true;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!validarFormulario()) return;
    
    setLoading(true);
    
    try {
      // 1. Registrar cliente
      const clienteResponse = await api.post('/api/clientes/publico', {
        ...formData,
        origem: 'QR_CODE_PADARIA',
        dataHoraCadastro: new Date().toISOString(),
        ipOrigem: window.location.hostname
      });
      
      const clienteId = clienteResponse.data.id;
      
      // 2. Registrar consentimentos LGPD
      for (const [finalidade, consentimento] of Object.entries(consentimentos)) {
        if (consentimento) { // Só registra consentimentos dados
          await api.post('/api/public/lgpd/consentimento', {
            usuarioId: clienteId,
            finalidade: finalidade.toUpperCase(),
            consentimento,
            ipOrigem: window.location.hostname
          });
        }
      }
      
      // 3. Log de auditoria
      await api.post('/api/auditoria/log', {
        usuarioId: clienteId,
        acao: 'CADASTRO_CLIENTE_PUBLICO',
        detalhes: 'Cliente cadastrado via QR Code com consentimentos LGPD',
        ipOrigem: window.location.hostname
      });
      
      setShowSuccess(true);
      
      // Redirecionar para cardápio após 3 segundos
      setTimeout(() => {
        navigate('/cliente/cardapio', { 
          state: { clienteId, nomeCliente: formData.nome }
        });
      }, 3000);
      
    } catch (error) {
      console.error('Erro ao cadastrar cliente:', error);
      alert('Erro ao realizar cadastro. Tente novamente.');
    } finally {
      setLoading(false);
    }
  };

  if (showSuccess) {
    return (
      <ClienteContainer>
        <WelcomeCard>
          <div style={{ padding: '40px', textAlign: 'center' }}>
            <div style={{ fontSize: '4rem', marginBottom: '20px' }}>🎉</div>
            <SuccessMessage>
              <h2 style={{ margin: '0 0 15px 0', color: '#155724' }}>
                Cadastro realizado com sucesso!
              </h2>
              <p style={{ margin: 0 }}>
                Bem-vindo(a) à Padaria Santa Marcelina, {formData.nome}!<br/>
                Redirecionando para o cardápio...
              </p>
            </SuccessMessage>
            <div style={{ marginTop: '20px', color: '#6c757d' }}>
              <p>✓ Seus dados foram registrados com segurança</p>
              <p>✓ Consentimentos LGPD registrados</p>
              <p>✓ Acesso ao cardápio digital liberado</p>
            </div>
          </div>
        </WelcomeCard>
      </ClienteContainer>
    );
  }

  return (
    <ClienteContainer>
      <WelcomeCard>
        <CardHeader>
          <LogoPadaria>
            <h1>🥖 Padaria Santa Marcelina</h1>
            <p>Bem-vindo ao nosso cardápio digital!</p>
          </LogoPadaria>
          
          <QRCodeInfo>
            <div className="qr-icon">📱</div>
            <p>Você acessou através do QR Code da mesa</p>
          </QRCodeInfo>
        </CardHeader>

        <div style={{ padding: '0 30px 30px' }}>
          <Form onSubmit={handleSubmit}>
            <h2 style={{ color: '#495057', marginBottom: '20px' }}>
              Complete seu cadastro para continuar
            </h2>

            <Input
              label="Nome Completo"
              name="nome"
              value={formData.nome}
              onChange={handleInputChange}
              placeholder="Digite seu nome completo"
              required
            />

            <Input
              label="Email"
              name="email"
              type="email"
              value={formData.email}
              onChange={handleInputChange}
              placeholder="seu-email@exemplo.com"
              required
            />

            <Input
              label="Telefone"
              name="telefone"
              value={formData.telefone}
              onChange={handleInputChange}
              placeholder="(11) 99999-9999"
              required
            />

            <Input
              label="CPF"
              name="cpf"
              value={formData.cpf}
              onChange={handleInputChange}
              placeholder="000.000.000-00"
              required
            />

            <Input
              label="Data de Nascimento"
              name="dataNascimento"
              type="text"
              value={formData.dataNascimento}
              onChange={handleInputChange}
              placeholder="dd/mm/aaaa"
              maxLength="10"
            />

            <Input
              label="Endereço"
              name="endereco"
              value={formData.endereco}
              onChange={handleInputChange}
              placeholder="Rua, número, complemento"
            />

            <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '16px' }}>
              <Input
                label="Bairro"
                name="bairro"
                value={formData.bairro}
                onChange={handleInputChange}
                placeholder="Seu bairro"
              />
              <Input
                label="CEP"
                name="cep"
                value={formData.cep}
                onChange={handleInputChange}
                placeholder="00000-000"
              />
            </div>

            {/* Seção de Consentimentos LGPD */}
            <ConsentimentoSection>
              <ConsentimentoTitle>
                Gestão de Privacidade e Consentimentos
              </ConsentimentoTitle>
              
              <p style={{ marginBottom: '20px', color: '#6c757d', fontSize: '0.9rem' }}>
                Conforme a Lei Geral de Proteção de Dados (LGPD), precisamos do seu consentimento 
                para algumas formas de uso dos seus dados:
              </p>

              <ConsentimentoItem>
                <input
                  type="checkbox"
                  checked={consentimentos.servicoEssencial}
                  disabled
                />
                <ConsentimentoText className="obrigatorio">
                  <strong>Serviços Essenciais</strong>
                  <small>
                    Processamento de pedidos, atendimento ao cliente e gestão da conta. 
                    Base legal: execução de contrato.
                  </small>
                </ConsentimentoText>
              </ConsentimentoItem>

              <ConsentimentoItem>
                <input
                  type="checkbox"
                  checked={consentimentos.marketingEmail}
                  onChange={() => handleConsentimentoChange('marketingEmail')}
                />
                <ConsentimentoText>
                  <strong>Marketing por Email</strong>
                  <small>
                    Receber ofertas especiais, novidades e promoções por email. 
                    Você pode revogar a qualquer momento.
                  </small>
                </ConsentimentoText>
              </ConsentimentoItem>

              <ConsentimentoItem>
                <input
                  type="checkbox"
                  checked={consentimentos.marketingSMS}
                  onChange={() => handleConsentimentoChange('marketingSMS')}
                />
                <ConsentimentoText>
                  <strong>Marketing por SMS</strong>
                  <small>
                    Receber ofertas e avisos importantes via SMS/WhatsApp.
                  </small>
                </ConsentimentoText>
              </ConsentimentoItem>

              <ConsentimentoItem>
                <input
                  type="checkbox"
                  checked={consentimentos.analisesPersonalizadas}
                  onChange={() => handleConsentimentoChange('analisesPersonalizadas')}
                />
                <ConsentimentoText>
                  <strong>Análises Personalizadas</strong>
                  <small>
                    Usar IA para analisar suas preferências e sugerir produtos personalizados.
                  </small>
                </ConsentimentoText>
              </ConsentimentoItem>

              <ConsentimentoItem>
                <input
                  type="checkbox"
                  checked={consentimentos.cookiesAnalytics}
                  onChange={() => handleConsentimentoChange('cookiesAnalytics')}
                />
                <ConsentimentoText>
                  <strong>Cookies de Analytics</strong>
                  <small>
                    Melhorar a experiência do site através de análise de navegação.
                  </small>
                </ConsentimentoText>
              </ConsentimentoItem>
            </ConsentimentoSection>

            {/* Termos de Uso */}
            <TermosSection>
              <div style={{ display: 'flex', alignItems: 'flex-start', gap: '12px' }}>
                <input
                  type="checkbox"
                  checked={aceitouTermos}
                  onChange={(e) => setAceitouTermos(e.target.checked)}
                  style={{ marginTop: '2px' }}
                />
                <div>
                  <p style={{ margin: 0 }}>
                    Li e aceito os{' '}
                    <a 
                      href="/termos-uso" 
                      target="_blank" 
                      style={{ color: '#0056b3', textDecoration: 'underline' }}
                    >
                      Termos de Uso
                    </a>
                    {' '}e a{' '}
                    <a 
                      href="/politica-privacidade" 
                      target="_blank"
                      style={{ color: '#0056b3', textDecoration: 'underline' }}
                    >
                      Política de Privacidade
                    </a>
                    {' '}da Padaria Santa Marcelina. *
                  </p>
                </div>
              </div>
            </TermosSection>

            <ButtonGroup>
              <Button
                type="button"
                variant="secondary"
                onClick={() => window.history.back()}
                style={{ flex: 1 }}
              >
                Voltar
              </Button>
              <Button
                type="submit"
                disabled={loading || !aceitouTermos}
                style={{ flex: 2 }}
              >
                {loading ? <Spinner size="small" /> : 'Continuar para o Cardápio'}
              </Button>
            </ButtonGroup>
          </Form>
        </div>
      </WelcomeCard>
    </ClienteContainer>
  );
};